import React, {useState, useEffect} from "react";
import ContainerHeader from 'components/ContainerHeader/index';
import IntlMessages from 'util/IntlMessages';
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableFooter from "@material-ui/core/TableFooter";
import TableHead from "@material-ui/core/TableHead";
import TablePagination from "@material-ui/core/TablePagination";
import TableRow from "@material-ui/core/TableRow";
import TableSortLabel from "@material-ui/core/TableSortLabel";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import IconButton from "@material-ui/core/IconButton";
import Tooltip from "@material-ui/core/Tooltip";
import PlaylistPlayIcon from "@material-ui/icons/PlaylistAdd";
import CardEditMenu from './CardEditMenu.js'
import SearchBox from '../../../../../components/SearchBox/index';
import {useSelector} from 'react-redux';
import {useHistory} from 'react-router-dom';
import CircularProgress from '@material-ui/core/CircularProgress';
import {base_url,public_url, $http} from "config";
import {Modal, ModalHeader} from 'reactstrap';
import CloseIcon from '@material-ui/icons/Close';
import TextField from '@material-ui/core/TextField';
import MenuItem from '@material-ui/core/MenuItem';
import Button from '@material-ui/core/Button';
import Fab from '@material-ui/core/Fab';

import {NotificationContainer, NotificationManager} from 'react-notifications';

const columnData = [
  {id: "id", align: false, disablePadding: true, label: "ID"},
  {id: "building_name", align: true, disablePadding: false, label: "Building Name"},
  {id: "department_name", align: true, disablePadding: false, label: "Department_name"},
  {id: "address", align: true, disablePadding: false, label: "Address"},
  {id: "postcode", align: true, disablePadding: false, label: "Postcode"},
  {id: "floors_count", align: true, disablePadding: false, label: "Floors"},
  {id: "rooms_count", align: true, disablePadding: false, label: "Rooms"},
  {id: "actions", align: true, disablePadding: false, numeric: 'right', label: "Actions"}
];
const SiteInfo = (props) => {
  const site_id = props.location.state ? props.location.state.id : null;
  const [site_open, setSiteOpen] = useState(false);
  const [building_open, setBuildingOpen] = useState(false);
  const [order, setOrder] = useState("asc");
  const [orderBy, setOrderBy] = useState("id");
  const [loader, setLoader] = useState(false);
  const {width} = useSelector(({settings}) => settings);
  const History = useHistory();

  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(10);
  const [tabledata, setTabledata] = useState([]);

  const [customername_rows, setCustomerNames] = useState([]);
  const [customer_id, setCustomerName] = useState(null);
  const [site_name, setSiteName] = useState("");
  const [site_address, setSiteAddress] = useState("");
  const [site_city, setSiteCity] = useState("");
  const [site_postcode, setSitePostcode] = useState("");
  const [site_contact_name, setSiteContactName] = useState("");
  const [out_contact, setOutContacct] = useState("");
  const [any_site, setAnySite] = useState("");
  const [hours_access, setHoursAccess] = useState("");
  const [site_comment, setSiteComment] = useState("");
  const [site,setSite] = useState([]);
  const [buildings,setBuildings] = useState([]);

  const [departmentname_rows, setDepartmentNames] = useState([]);
  const [department_id, setDepartmentName] = useState(null);
  const [building_id, setBuildingId] = useState(null);
  const [building_name, setBuildingName] = useState("");
  const [building_address, setBuildingAddress] = useState("");
  const [building_postcode, setbuildingPostcode] = useState("");
  const [building_upload_img, setBuildingUploadImg] = useState(null);
  const [building_upload_img_url, setBuildingUploadImgUrl] = useState('https://via.placeholder.com/300x300');

  useEffect(() => {
    init_Data();
  }, []);

  const init_Data = () => {
    $http.get(base_url + "site/siteInfo", {params : {id: site_id}})
    .then((response) => {
      console.log(response.data);
      setTabledata(response.data.buildings);
      setBuildings(response.data.buildings);
      setSite(response.data.site);
      setLoader(true);
    });
  }

  const createSortHandler = property => event => {
    handleRequestSort(event, property);
  };

  const handleRequestSort = (event, property) => {
    let sort_order;
    if (orderBy === property && order === "asc") {
      sort_order = "desc";
    } else {
      sort_order = "asc";
    }

    
    sort_order === "desc"
      ? buildings.sort((a, b) => (b[property] < a[property] ? -1 : 1))
      : buildings.sort((a, b) => (a[property] < b[property] ? -1 : 1));

    
    setOrder(sort_order);
    setOrderBy(property);
  };

  const handleClick = (event, id) => {
    // History.push({
    //   pathname: "/app/sites/site/Info",
    //   state: {id: id}
    // })
  };

  const handleChangePage = (event, page) => {
    setPage(page);
  };

  const handleChangeRowsPerPage = event => {
    setRowsPerPage(event.target.value);
  };
  const onSelectBuilding = (data, index) => {
    if (index === 0) {
      History.push({
        pathname: "/app/sites/site/building",
        state: {id: data,site_id:site_id}
      })
    } else if (index === 1) {
      BuildingModalOpen(data);
    } else if (index === 2) {
      $http.post(base_url + "building/deleteBuilding", {id: data})
      .then((response) => {
        init_Data();
        NotificationManager.info("You removed selected Building");
      })
    }
  }

  const SearchBuilding = (data) => {
    setBuildings(
      tabledata.filter(function (item) {
        return (item.building_name.toLowerCase().includes(data.toLowerCase()));
      })
    );
  }
  const SiteDelete = (data)=>{
    $http.post(base_url + "site/deleteSite", {params : {id: data}})
    .then((response) => {
       History.push({
        pathname: "/app/sites/site",
        state: {}
      })

    })
  }
  const SiteModalOpen = (data) => {
    $http.get(base_url + "site/getSiteInfo", {params : {id: data}})
    .then((response) => {
      console.log(response.data);
        setCustomerNames(response.data.customers);
        if (data > 0) {
          setCustomerName(response.data.site.company_id);
          setSiteName(response.data.site.site_name);
          setSiteAddress(response.data.site.address);
          setSiteCity(response.data.site.city);
          setSitePostcode(response.data.site.postcode);
          setSiteContactName(response.data.site.contact_name);
          setOutContacct(response.data.site.contact_number);
          setAnySite(response.data.site.site_instructions);
          setHoursAccess(response.data.site.access_hour);
          setSiteComment(response.data.site.comment);
        }
        setSiteOpen(true);
    })
  }
  const SiteModalClose = () => {
    setSiteOpen(false);
  }
  const SiteModalSave = () => {
    SiteModalClose();
      const frmData = new FormData();
      frmData.append('id', site_id);
      frmData.append('customer_id', customer_id);
      frmData.append('site_name', site_name);
      frmData.append('address', site_address);
      frmData.append('city', site_city);
      frmData.append('postcode', site_postcode);
      frmData.append('contact_name', site_contact_name);
      frmData.append('contact_number', out_contact);
      frmData.append('site_instructions', any_site);
      frmData.append('access_hour', hours_access);
      frmData.append('comment', site_comment);

      $http.post(base_url + "site/updateSite", frmData)
      .then((response) => {
        console.log(response.data);
          if (response.data.status === "success") {
              NotificationManager.success(response.data.msg, "Success!", 5000, () => {
              });
              init_Data();
          } else if (response.data.status === "error") {
              NotificationManager.error(response.data.msg, "Error!", 5000, () => {
              });
          }
      }).catch((error)=>{
          console.log("error:", error);
      })
  }
  const BuildingModalOpen = (data) => {
    setBuildingId(data);
    $http.get(base_url + "building/getBuildingInfo", {params : {id: data,site_id:site_id}})
    .then((response) => {
      console.log(response.data);
        setDepartmentNames(response.data.departments);
        if (data > 0) {
          setDepartmentName(response.data.building.department_id);
          setBuildingName(response.data.building.building_name);
          setBuildingAddress(response.data.building.address);
          setbuildingPostcode(response.data.building.postcode);
          setBuildingUploadImgUrl(response.data.building.upload_img ? 
            (public_url + 'upload/img/' + response.data.building.upload_img) : 
            'https://via.placeholder.com/300x300');
        }
        setBuildingOpen(true);
    })
  }
  const BuildingModalClose = () => {
    setBuildingOpen(false);
  }
  const BuildingModalSave = () => {
    BuildingModalClose();
      const frmData = new FormData();
      frmData.append('id', building_id);
      frmData.append('site_id', site_id);
      frmData.append('department_id', department_id);
      frmData.append('building_name', building_name);
      frmData.append('address', building_address);
      frmData.append('postcode', building_postcode);
      frmData.append('upload_img', building_upload_img);
     

      $http.post(base_url + "building/updateBuilding", frmData)
      .then((response) => {
        console.log(response.data);
          if (response.data.status === "success") {
              NotificationManager.success(response.data.msg, "Success!", 5000, () => {
              });
              init_Data();
          } else if (response.data.status === "error") {
              NotificationManager.error(response.data.msg, "Error!", 5000, () => {
              });
          }
      }).catch((error)=>{
          console.log("error:", error);
      })
  }
  const changeUploadImg = (e) =>{
    e.preventDefault();

    let reader = new FileReader();
    let file = e.target.files[0];
    reader.onloadend = () => {
        setBuildingUploadImg(file);
        setBuildingUploadImgUrl(reader.result);
    }
    reader.readAsDataURL(file)
}

  return (
    <div className="app-wrapper">
      <ContainerHeader match={props.match} title={<IntlMessages id="sidebar.sites.siteInfo"/>}/>
      { loader ? (
          <div className="row">
              <div className="col-12">
                  <div className="jr-card">
                      <div className="row">
                          <div className="col-xl-12 col-lg-12 col-md-6 col-sm-6 col-12">
                              <div className="row">
                                  <div className="col-xl-4 col-lg-4 col-md-6 col-sm-6 col-12 text-right">
                                      <h2><strong>Site Name</strong></h2>
                                  </div>
                                  <div className="col-xl-8 col-lg-8 col-md-6 col-sm-6 col-12 text-left">
                                      <h2>{site.site_name}</h2>
                                  </div>
                                  <div className="col-xl-4 col-lg-4 col-md-6 col-sm-6 col-12 text-right">
                                      <h2><strong>Address</strong></h2>
                                  </div>
                                  <div className="col-xl-8 col-lg-8 col-md-6 col-sm-6 col-12 text-left">
                                      <h2>{site.address}</h2>
                                  </div>
                                  <div className="col-xl-4 col-lg-4 col-md-6 col-sm-6 col-12 text-right">
                                      <h2><strong>Contact Number</strong></h2>
                                  </div>
                                  <div className="col-xl-8 col-lg-8 col-md-6 col-sm-6 col-6 text-left">
                                      <h2>{site.contact_number}</h2>
                                  </div>
                              </div>
                          </div>
                          <ul className="list-inline d-sm-flex flex-sm-row jr-mbtn-list mb-0 jr-featured-content-right">
                              <li><Button color="primary" onClick={() => SiteModalOpen(site_id)}><IntlMessages id="button.modify"/></Button></li>
                              <li><Button color="secondary" onClick={() => SiteDelete()}><IntlMessages id="button.delete"/></Button></li>
                          </ul>
                      </div>
                  </div>
              </div>
              <div className="col-12">
                  <div className="jr-card">
                    <div>
                      <Toolbar className="table-header">
                        <div className="title">
                          <Typography variant="h6">Buildings</Typography>
                        </div>
                        <div className="spacer">
                            <div className="col-md-3 col-lg-3 col-sx-6 col-6 ml-auto">
                                <SearchBox placeholder="Search ..."
                                  onChange={(event) => SearchBuilding(event.target.value)}
                                />
                            </div>
                        </div>
                        <div className="actions">
                          <Tooltip title="New Building">
                            <IconButton aria-label="New Building" onClick={() => BuildingModalOpen(null)}>
                                <PlaylistPlayIcon/>
                            </IconButton>
                          </Tooltip>
                        </div>
                      </Toolbar>
                      {
                        loader ? (
                          <div className="flex-auto">
                            <div className="table-responsive-material">
                              <Table className="">
                                <TableHead>
                                  <TableRow>
                                    {columnData.map(column => {
                                      return (
                                        <TableCell
                                          key={column.id}
                                          align={column.numeric}
                                        >
                                          <Tooltip
                                            title="Sort"
                                            placement={column.numeric ? "bottom-end" : "bottom-start"}
                                            enterDelay={300}
                                          >
                                              <TableSortLabel
                                              active={orderBy === column.id}
                                              direction={order}
                                              onClick={createSortHandler(column.id)}
                                            >
                                              {column.label}
                                            </TableSortLabel>
                                          </Tooltip>
                                        </TableCell>
                                      );
                                    })}
                                  </TableRow>
                                </TableHead>
                                <TableBody>
                                  {buildings.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage).map((n) => 
                                      <TableRow
                                        hover
                                        key={n.id}
                                        onClick={event => handleClick(event, n.id)}
                                        //onKeyDown={event => handleKeyDown(event, n.id)}
                                        tabIndex={-1}
                                      >
                                        <TableCell>{n.id}</TableCell>
                                        <TableCell>{n.building_name}</TableCell>
                                        <TableCell>{n.department_name}</TableCell>
                                        <TableCell>{n.address}</TableCell>
                                        <TableCell>{n.postcode}</TableCell>
                                        <TableCell>{n.floors_count}</TableCell>
                                        <TableCell>{n.rooms_count}</TableCell>
                                        <TableCell className="text-right">
                                          <CardEditMenu key={n.id} selectedData={n.id} 
                                            onSelectRow={onSelectBuilding}/>
                                        </TableCell>
                                      </TableRow>
                                    )}
                                </TableBody>
                                <TableFooter>
                                  <TableRow>
                                    <TablePagination
                                      count={buildings.length}
                                      rowsPerPage={rowsPerPage}
                                      page={page}
                                      onChangePage={handleChangePage}
                                      onChangeRowsPerPage={handleChangeRowsPerPage}
                                    />
                                  </TableRow>
                                </TableFooter>
                              </Table>
                            </div>
                            <NotificationContainer/>
                          </div>
                        ):(
                          <div className="loader-view"
                                  style={{height: width >= 1200 ? 'calc(100vh - 259px)' : 'calc(100vh - 238px)'}}>
                              <CircularProgress/>
                          </div>        
                        )
                      }
                      <Modal key="building-save" className="modal-box" toggle={BuildingModalClose} isOpen={building_open} fullwidth="true">
                          <ModalHeader>{building_id ? 'Modify Building':'Add Building'}
                              <IconButton className="text-grey"
                                          onClick={BuildingModalClose}>
                                  <CloseIcon/>
                              </IconButton>
                          </ModalHeader>
                          <div className="modal-box-content">
                              <div className="row">
                                  <div className="col-xl-3 col-lg-4 col-md-5 col-12">
                                      <div className="jr-card pb-2">
                                          <div className="jr-card-thumb">
                                              <img className="card-img-top img-fluid" alt="products" src={building_upload_img_url}/>
                                          </div>
                                          <input type="file" id="upload_img" accept="image/*" 
                                              name="upload_img" style={{display: "none"}}
                                              onChange={(event) => changeUploadImg(event)}/>

                                          <div className="jr-card-social text-right">
                                              <Fab className="jr-fab-btn bg-secondary text-white jr-btn-fab-xs mb-3" 
                                                  onClick={(e)=>{document.getElementById('upload_img').click()}}>
                                                  <i className="zmdi zmdi-cloud-upload zmdi-hc-1x"/>
                                              </Fab>
                                          </div>
                                      </div>
                                  </div>
                                  <div className="col-xl-9 col-lg-8 col-md-7 col-12">
                                      <div className="row">
                                      <div className="col-md-6 col-12">
                                      <TextField
                                          id="departments"
                                          select
                                          label="departments"
                                          value={department_id}
                                          onChange={(event)=>setDepartmentName(event.target.value)}
                                          SelectProps={{}}
                                          helperText="Please select Department"
                                          margin="normal"
                                          fullWidth
                                      >
                                          {departmentname_rows.map(option => (
                                              <MenuItem key={option.id} value={option.id}>
                                                  {option.department_name}
                                              </MenuItem>
                                          ))}
                                      </TextField>
                                  </div>
                                  <div className="col-md-6 col-12">
                                      <TextField 
                                          id="building_name"
                                          label="Building Name"
                                          value={building_name}
                                          onChange={(event)=>setBuildingName(event.target.value)}
                                          margin="normal"
                                          helperText="Please Enter Building Name"
                                          fullWidth
                                          />
                                  </div>
                                  <div className="col-md-6 col-12">
                                      <TextField 
                                          id="address"
                                          label="Address"
                                          value={building_address}
                                          onChange={(event)=>setBuildingAddress(event.target.value)}
                                          margin="normal"
                                          helperText="Please Enter Address"
                                          fullWidth
                                          />
                                  </div>
                                  <div className="col-md-6 col-12">
                                      <TextField 
                                          id="postcode"
                                          label="Postcode"
                                          value={building_postcode}
                                          onChange={(event)=>setbuildingPostcode(event.target.value)}
                                          margin="normal"
                                          helperText="Please Enter Postcode"
                                          fullWidth
                                          />
                                  </div>
                                  
                                  
                                  </div>
                                  </div>

                              </div>
                          </div>
                          <div className="modal-box-footer d-flex flex-row">
                              <Button onClick={BuildingModalClose} color="secondary">
                              Cancel
                              </Button>
                              <Button onClick={BuildingModalSave} color="primary">
                              Save
                              </Button>
                          </div>
                      </Modal>
                      <Modal key="site-save" className="modal-box" toggle={SiteModalClose} isOpen={site_open}>
                        <ModalHeader>{site_id ? 'Modify Site':'Add Site'}
                            <IconButton className="text-grey"
                                        onClick={SiteModalClose}>
                                <CloseIcon/>
                            </IconButton>
                        </ModalHeader>
                        <div className="modal-box-content">
                            <div className="row">
                                <div className="col-md-6 col-12">
                                    <TextField
                                        id="custemername"
                                        select
                                        label="Customer Name"
                                        value={customer_id}
                                        onChange={(event)=>setCustomerName(event.target.value)}
                                        SelectProps={{}}
                                        helperText="Please select Customer Name"
                                        margin="normal"
                                        fullWidth
                                    >
                                        {customername_rows.map(option => (
                                            <MenuItem key={option.id} value={option.id}>
                                                {option.name}
                                            </MenuItem>
                                        ))}
                                    </TextField>
                                </div>
                                
                                <div className="col-md-6 col-12">
                                    <TextField 
                                        id="site_name"
                                        label="Site"
                                        value={site_name}
                                        onChange={(event) => setSiteName(event.target.value)}
                                        margin="normal"
                                        helperText="Please Enter Site"
                                        fullWidth
                                        />
                                </div>
                              
                                <div className="col-md-6 col-12">
                                        <TextField 
                                            id="address"
                                            label="Address"
                                            value={site_address}
                                            onChange={(event)=>setSiteAddress(event.target.value)}
                                            margin="normal"
                                            helperText="Please Enter Address"
                                            fullWidth
                                            />
                                    </div>
                                    <div className="col-md-6 col-12">
                                        <TextField 
                                            id="site_city"
                                            label="City"
                                            value={site_city}
                                            onChange={(event)=>setSiteCity(event.target.value)}
                                            margin="normal"
                                            helperText="Please Enter City"
                                            fullWidth
                                            />
                                    </div>
                                    <div className="col-md-6 col-12">
                                        <TextField 
                                            id="site_postcode"
                                            label="Postcode"
                                            value={site_postcode}
                                            onChange={(event)=>setSitePostcode(event.target.value)}
                                            margin="normal"
                                            helperText="Please Enter Postcode"
                                            fullWidth
                                        />
                                    </div>
                                    <div className="col-md-6 col-12">
                                        <TextField 
                                            id="site_contact_name"
                                            label="Site Contact Name"
                                            value={site_contact_name}
                                            onChange={(event)=>setSiteContactName(event.target.value)}
                                            margin="normal"
                                            helperText="Please Enter Site Contact Name"
                                            fullWidth
                                            />
                                    </div>
                                    <div className="col-md-6 col-12">
                                        <TextField 
                                            id="out_contact"
                                            label="Out of ours contact number"
                                            value={out_contact}
                                            onChange={(event)=>setOutContacct(event.target.value)}
                                            margin="normal"
                                            helperText="Please Enter Out of ours contact number"
                                            fullWidth
                                            />
                                    </div>
                                    <div className="col-md-6 col-12">
                                        <TextField 
                                            id="any_site"
                                            label="Any site instructions"
                                            value={any_site}
                                            onChange={(event)=>setAnySite(event.target.value)}
                                            margin="normal"
                                            helperText="Please Enter Any site instructions"
                                            fullWidth
                                            />
                                    </div>
                                    <div className="col-md-6 col-12">
                                        <TextField 
                                            id="hours_access"
                                            label="Hours of access"
                                            value={hours_access}
                                            onChange={(event)=>setHoursAccess(event.target.value)}
                                            margin="normal"
                                            helperText="Please Enter Hours of access"
                                            fullWidth
                                            />
                                    </div>
                                    <div className="col-md-12 col-12">
                                        <TextField
                                            id="site_comment"
                                            label="Comments"
                                            multiline
                                            rowsMax="4"
                                            value={site_comment}
                                            onChange={(event) => setSiteComment(event.target.value)}
                                            margin="normal"
                                            fullWidth
                                        />
                                    </div>
                              
                            </div>
                        </div>
                        <div className="modal-box-footer d-flex flex-row">
                            <Button onClick={SiteModalClose} color="secondary">
                            Cancel
                            </Button>
                            <Button onClick={SiteModalSave} color="primary">
                            Save
                            </Button>
                        </div>
                      </Modal>
                    </div>
                  </div>
              </div>  
          </div>
          ) :
          (
            <div className="loader-view"
                    style={{height: width >= 1200 ? 'calc(100vh - 259px)' : 'calc(100vh - 238px)'}}>
                <CircularProgress/>
            </div>
          )}
    </div>
  );
};

export default SiteInfo;
